export default {
  name: "TipsDialog",
  props: {
    tipsVisible: {
      type: Boolean,
      default: false,
    },
    content: {
      type: String,
      default: '未知错误',
    },
  },
  data () {
    return {
      visible: this.tipsVisible,
    }
  },
  watch: {
    tipsVisible () {
      this.visible = this.tipsVisible
    },
  },
}
